	export default {
		data() {
			return {
				info:{
					pics: [{
						colorClass: 'none',
						url: ''
					}]
				},
				dotsStyles: {
					backgroundColor: 'rgba(249, 249, 251, 1)',
					border: 'none',
					color: '#fff',
					selectedBackgroundColor: 'rgba(3, 190, 144, 1)',
					selectedBorder: 'none'
				},
				mode: 'dot',
				current:0,
                load:false
			};
		},
		onLoad(option) {
			this.getData()
		},
		computed: {
			communityId(){
				return this.$store.getters.communityId
			}
		},
		watch: {
			info:{
				handler(val,oldVal){
					if(val && val.length>0){
						if(val.markStatus){
							this.options[2] = {icon: 'star-filled',color: '#F7CF41'}
						}else{
							this.options[2] = {icon: 'star',color: '#B7B7B7'}
						}
					}				
					
				},
				immediate: true, 
				deep: true
			}
		},
		methods: {
			change(e) {
				this.current = e.detail.current
			},
			getData() {
				this.$api.findByCommunityId({
					communityId: this.communityId
				}).then(res=>{
					this.info.pics = res.data
					this.info.pics.map(item=>{
						item.pics = JSON.parse(item.pics)
					})
                    this.load = true
				})
			}
		},
		
	}
